@import url('https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans&display=swap');


body {
  font-size  : 16px;
  background : white;
  font-family: 'Open Sans', sans-serif;
  overflow-x : hidden;
  z-index    : 1;
}


.App {
  text-align: center;
}

.flag-size {
  width      : 40px;
  padding-top: 10px;
}



.App-header {
  position      : relative;
  min-height    : 100vh;
  display       : flex;
  flex-direction: column;
  align-items   : center;
  color         : black;
  top           : 4em;
}

.heading-background {
  position : fixed;
  opacity  : 0.15;
  font-size: 25em;
  left     : -0.2em;
  top      : -0.5em;
  z-index  : -100;
  color    : #9352ae;
}

.heading-background img {
  position: fixed;
  top     : 0;
  left    : 0;
  width   : 100%;
}

header {
  display        : flex;
  flex-direction : row;
  justify-content: center;
  align-items    : center;
}

header h1 {
  font-size   : 1em;
  font-weight : 400;
  padding     : 2em;
  margin-right: 50em;
}

nav {
  position: fixed;
  /* left    : 50em; */
  left    : 44em;
  top     : 6em;
  z-index : 100;
}

nav li {
  display: inline-block;
  cursor : pointer;

}

nav li .link {
  text-decoration   : none;
  color             : black;
  padding           : 1em;
  opacity           : 0.5;
  transition        : all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition   : all 0.3s ease-in-out;
  -ms-transition    : all 0.3s ease-in-out;
  -o-transition     : all 0.3s ease-in-out;
}

nav li .active {
  opacity    : 1;
  font-weight: bold;
  color      : #9352ae;
}

li .link:hover {
  opacity: 1;
}


.header-title button {
  background        : black;
  border            : 0px;
  padding           : 1em 3em;
  transition        : all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition   : all 0.2s ease-out;
  -ms-transition    : all 0.2s ease-out;
  -o-transition     : all 0.2s ease-out;
}

.header-title button:hover {
  opacity: 0.8;
}

.header-title button a {
  font-family    : 'Open Sans', sans-serif;
  color          : white;
  text-decoration: none;
}

.header-title {
  position   : relative;
  font-family: 'Montserrat', sans-serif;
  font-size  : 3em;
  text-align : left;
  top        : 2em;
}

.about {
  display       : flex;
  flex-direction: row;
  margin-top    : 5em;
}

.about-content {
  position: relative;
  left    : 2em;
  top     : 1em;
}

.about-content h1 {
  font-family: 'Montserrat', sans-serif;
  font-size  : 5em;
  text-align : left;
}

.about-content h3 {
  text-align: left;
}

.about-content p {
  font-size : 1.3em;
  text-align: left;
  width     : 31em;
}

.about img {
  margin-left: 5em;
  margin-top : 5em;

  border-radius: 10%;
  border       : 2px solid rgba(00, 00, 00, 0.35)
}

.work {
  position      : relative;
  display       : flex;
  flex-direction: column;
  left          : 0em;
  top           : 5em;
  align-items   : flex-start;
}

.work div {
  width: 100%;
}

.work .timeline {
  position: relative;
}

.work .heading {
  font-family: 'Montserrat', sans-serif;
  font-size  : 5em;
  text-align : center;
}

.work .work-content {
  display              : grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap             : 2em;
}

.project img {
  width : 30em;
  height: 30em;
}

.contact {
  position  : relative;
  top       : 20em;
  left      : 7em;
  text-align: left;
}


.contact .c1 {
  width: 60%;
  float: left;
}

.contact .c1 h1 {
  font-size: 4em;
  padding  : 1em 0 0.5em;
}

.contact .c2 {
  width         : 40%;
  float         : right;
  padding-bottom: 5em;
}

.contact h1 {
  font-family: 'Montserrat', sans-serif;
  font-size  : 5em;
  text-align : left;
}

.amazing-color {
  color  : #9352ae;
  opacity: 1;
}

.contact .email {
  color    : black;
  font-size: 2em;
}

.contact li {
  display      : inline-block;
  margin-top   : 0;
  margin-bottom: 0;
  margin-right : 1em;
  margin-left  : 0;
}

.contact li a {
  text-decoration: none;
  color          : black;
}

.footer {
  position: relative;
  top     : 2em;
  padding : 1em;
}

.footer a {
  text-decoration: none;
  color          : #2c59fb;
}

.certificates,
.projects,
.skill {
  width : 95%;
  margin: 10em auto 0;
}

.certificates .heading,
.projects .heading,
.skill .heading {
  font-family: 'Montserrat', sans-serif;
  font-size  : 5em;
  text-align : center;
}

.ResumeSkillsBlock-skills {
  display        : -ms-flexbox;
  display        : flex;
  -ms-flex-wrap  : wrap;
  flex-wrap      : wrap;
  -ms-flex-pack  : justify;
  justify-content: space-around;
  /* space-between; */
}

.ResumeSkillsBlock-skills>div {
  width        : 23%;
  text-align   : center;
  margin-bottom: 15px;
}

.jss81 {
  overflow: hidden;
}

.jss12 {
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.jss10 {
  border-radius: 4px;
}

.jss9 {
  background-color: #fff;
}

.jss81 {
  overflow: hidden;
}

.jss12 {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .2), 0 1px 1px 0 rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.jss10 {
  border-radius: 4px;
}

.jss9 {
  background-color: #fff;
}

.jss82 {
  padding-bottom: 10px;
}

.vertical-timeline--two-columns .vertical-timeline-element:nth-child(odd) .vertical-timeline-element-date {
  text-align: left
}

.certificates .SVGInline,
.projects .SVGInline {
  padding: 5px;
}

.certificates .project-thumb,
.projects .project-thumb {
  height: 285px;
  width : 285px;
}

/* SMARTPHONES PORTRAIT */
@media only screen and (min-device-width: 320px) and (max-device-width : 480px) {

  body {
    font-size: 6px;
  }

  header h1 {
    font-size   : 5em;
    padding     : 1em;
    margin-right: 0em;
  }

  .heading-background {
    display: none;
  }

  nav {
    display: none;
  }

  .header-title {
    font-size : 3em;
    text-align: left;
    top       : 1em;
    left      : 1em;
  }

  .about {
    position: relative;
    display : flex;
    top     : -30em;
  }

  .about img {
    display: none
  }

  .about-content {
    left: 2em;
  }

  .about-content p {
    font-size : 3.5em;
    text-align: left;
    width     : 300px;
  }

  .work {
    left       : 2em;
    top        : 2em;
    align-items: flex-start;
  }

  .work .work-content {
    display              : grid;
    grid-template-columns: 1fr;
    grid-gap             : 5em;
  }

  .project img {
    width : 45em;
    height: 45em;
  }

  .contact {
    top : 15em;
    left: 2em;
  }

  .contact h1 {
    font-size: 4em;
  }

  .contact .email {
    font-size: 3.5em;
  }

  .contact li {
    display: block;
    padding: 2em;
  }

  .contact li a {
    font-size: 2.5em;
  }

  .footer {
    top      : 4em;
    padding  : 1em;
    font-size: 2em;
  }


}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {


  body {
    font-size: 10px;
  }

  header h1 {
    font-size   : 5em;
    padding     : 1em;
    margin-right: 0em;
  }

  nav {
    display: none;
  }

  .header-title {
    font-size : 3.6em;
    text-align: left;
    top       : 1em;
    left      : 1em;
  }

  .about {
    position: relative;
    display : flex;
    top     : -10em;
  }

  .about img {
    display: none
  }

  .about-content {
    left: 6em;
  }

  .about-content p {
    font-size : 3.5em;
    text-align: left;
    width     : 700px;
  }

  .work {
    left       : 6em;
    top        : 4em;
    align-items: flex-start;
  }

  .work .work-content {
    display              : grid;
    grid-template-columns: 1fr 1fr;
    grid-gap             : 5em;
  }

  .project img {
    width : 30em;
    height: 30em;
  }

  .contact {
    top : 15em;
    left: 6em;
  }

  .contact h1 {
    font-size: 4em;
  }

  .contact .email {
    font-size: 3.5em;
  }

  .contact li {
    display: block;
    padding: 2em;
  }

  .contact li a {
    font-size: 2.5em;
  }

  .footer {
    top      : 4em;
    padding  : 1em;
    font-size: 2em;
  }

}

/* Large screens ----------- */
@media only screen and (min-width : 1824px) {
  /* Styles */



  body {
    font-size: 20px;
  }

  header h1 {
    font-size   : 3em;
    margin-right: 25em;
  }


  nav {
    /* left   : 60em; */
    left   : 47em;
    top    : 12em;
    z-index: 100;
  }

  nav li {
    font-size: 3em;

  }

  .header-title {
    font-size: 5em;
    top      : 1em;
    left     : 1em;
  }

  .about {
    position: relative;
    display : flex;
    top     : -20em;
  }

  .about-content {
    left: 10em;
  }

  .about-content p {
    font-size : 3.5em;
    text-align: left;
    width     : 25em;
  }

  .about img {
    margin-left: 1em;
  }

  .work {
    left       : 10em;
    top        : 4em;
    align-items: flex-start;
  }

  .work .work-content {
    display              : grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .project img {
    width : 32em;
    height: 32em;
  }

  .contact {
    top : 10em;
    left: 8em;
  }

  .contact h1 {
    font-size: 8em;
  }

  .contact .email {
    font-size: 5em;
  }

  .contact li a {
    font-size: 4em;
    padding  : 1em;
  }

  .footer {
    top      : 4em;
    padding  : 1em;
    font-size: 4em;
  }


}